<template>
    <div class="gif d-flex align-items-start" :style="`background-image: url(${gif.images.original.url})`">
        <input readonly :value="gif.images.original.url" ref="url" type="text" class="form-control w-75">
        <button class="btn btn-primary" @click="copyUrl">Copy</button>
    </div>
</template>

<script>
export default {
    name: 'GifValue',
    props:{
        gif: Object,
    },
    methods:{
        copyUrl(){
            this.$refs.url.select()
            document.execCommand('copy')
            this.$refs.url.value = 'Copied!!!'
        }
    }
}
</script>

<style scoped>
    .gif{
        width: 240px;
        height: 240px;
        background-repeat: no-repeat;
        background-size: cover;
        margin: 10px auto;
    }
</style>