<template>
    <input v-model="keyword" @input="onInput" class=" form-control" type="text" placeholder="Type something to search for awesome gifs">
</template>

<script>
export default {
    name: 'SearchBox',
    data(){
        return{
            keyword: '',
            timeout: null,
        }
    },
    methods:{
        onInput(){
            clearTimeout(this.timeout)
            this.timeout = setTimeout(() =>{
                this.search()
            }, 500)
        },
        search(){
            fetch(`https://api.giphy.com/v1/gifs/search?api_key=Ny1C0E5l7vlRtBYcNloDrVftGPt3ZdUY&q=${this.keyword}&limit=12`)
                .then(response => response.json())
                .then(result => {
                    console.log(result);
                    this.$emit('fetch-gifs', result.data)
                })
        }
    }
}
</script>
